import Vue from 'vue';
import Router from 'vue-router';
import Inicio from './views/Inicio.vue';
import Testes from './views/Testes.vue';

Vue.use(Router);

export default new Router({
 mode: 'history',
 base: process.env.BASE_URL,
 routes: [
  {
   path: '/',
   name: 'inicio',
   component: Inicio
  },
  {
   path: '/contato',
   name: 'contato',
   component: () => import('./views/Contato.vue')
  },
  {
   path: '/metodologia',
   name: 'metodologia',
   component: () => import('./views/Metodologia.vue')
  },
  {
   path: '/materias',
   name: 'materias',
   component: () => import('./views/Materias.vue')
  },
  {
   path: '/material',
   name: 'material',
   component: () => import('./views/Material.vue')
  },
  {
   path: '/testes',
   name: 'testes',
   component: Testes
  }
 ]
});
